.cart-item {
    width: 100%;
    display: flex;
    height: 80px;
    margin-bottom: 15px;
    color: rgb(255, 204, 0);
    img {
      width: 30%;
    }
  
    .item-details {
      width: 70%;
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      justify-content: center;
      padding: 10px 20px;
  
      .name {
        font-size: 16px;
        color: rgb(255, 204, 0);
      }

      .price{
        font-size: 12px;
        color: rgba(255, 204, 0, 0.659);
      }
    }
  }
  